html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  background-color: #f5f5f5;
}

img {
  position: relative;
  max-width: 100%;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--color-dark);
    box-shadow: .5rem .5rem 3rem 1px rgba(0,0,0,0.05);
    opacity: 0.2;
 }
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}
h1,h2,h3,h4,h5,h6{
  margin: 0;
  padding: 0;
}
.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}
@import url("https://netdna.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css");

// CONTENT-CONTAINER====================================================================================================
.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
  
}
.container-full{
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1920px;
}
// =======================================================================================================
.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

// SITE-ELEMENTS==========================================================================================
.social-box{
  position: fixed;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  z-index: 100;
  &__item{
    position: relative;
    display: block;
    transition: transform 0.3s;
    &:not(:last-child):before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: linear-gradient(transparent,#fff,transparent);
    }
    & svg.social-icon{
      display: block;
      padding: 5px;
      width: 40px;
      height: 40px;
      fill: #fff;
      background: var(--color-blue);
      
    }
   
    &:hover{
      transform: translateX(-5px);
      
    }
  }
}

.p-50{
  padding: 50px 0;
}
.section-title{
  position: relative;
  display: inline-block;
  font-size: 36px;
  color: var(--color-dark);
  padding: 10px;
  transform: translateX(-30px);
  opacity: 0;
  transition: transform 0.4s ease-in, opacity 0.4s;
  transition-delay: 0.1s;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 3px;
    height: 100%;
    background: linear-gradient(to bottom, transparent,var(--color-blue),transparent);
  }
  &._active{
    transform: translateX(0);
  opacity: 1;
  }
}
.section-sub-title{
  position: relative;
  padding: 0px 0px 10px 45px;
  font-size: 18px;
  color: var(--color-dark);
  transform: translateX(30px);
  opacity: 0;
  transition: transform 0.4s ease-in, opacity 0.4s;
  &._active{
    transform: translateX(0);
  opacity: 1;
  }
}

.btn{
  display: inline-block;
  background: var(--grad-bg);
  padding: 10px 15px;
  letter-spacing: 1px;
  transition: transform 0.2s;
  &:hover{
    transform: scale(1.1);
  }
}

.page{
  position: relative;
  min-height: 100vh;
  &__container{
    position: relative;
    min-height: inherit;
    padding: 50px 15px 15px 15px;
    background: #fff;
    z-index: 10;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: url(https://www.wallpapertip.com/wmimgs/79-796674_professional-background-images-for-ppt-3-high-resolution.jpg);
      opacity: 0.2;
      z-index: -1;
    }
  }
  &__title{
    position: relative;
    display: block;
    text-align: center;
    padding: 10px 0;
    font-size: 32px;
    color: var(--color-dark);
    margin-bottom: 20px;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      width: 50%;
      height: 2px;
      background: var(--color-blue);
      transform: translateX(-50%);
    }
    
  }
  &__data{
    display: inline-block;
    font-size: 13px;
    color: var(--color-dark);
    font-style: italic;
    opacity: 0.5;
    & i{
      display: inline-block;
      margin-right: 7px;
      margin-bottom: 10px;
    }
  }
  &__img{
    float: left;
    width: 30%;
    height: auto;
    margin-right: 10px;
    & img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  & p{
    font-size: 16px;
    color: var(--color-dark);
    line-height: 1.5;
  }
  & a{
    color: var(--color-blue);
  }
}