@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// navigation
.nav{
  width: auto;
  height: 100px;
  & ul.nav-list{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    & .nav-item{
      position: relative;
      display: block;
      height: 100%;
      &:not(:last-child){
        margin-right: 15px;
      }
      & a.nav-link{
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        color: #fff;
        text-transform: uppercase;
        font-weight: 700;
        padding: 0 10px;
        background: transparent;
        border-bottom: 1px solid transparent;
        transition: background 0.2s, border-bottom 0.2s;
        &:hover{
          color: var(--color-blue);
          background: #fff;
        }
      }
      &.has-child{
        position: relative;
        & ul.sub-list{
          position: absolute;
          top: 100%;
          left: 0;
          width: 250%;
          height: auto;
          padding: 10px;
          background: #fff;
          opacity: 0;
          visibility: hidden;
          transition: transform 0.2s, opacity 0.25s;
          & li.sub-item{
            display: block;
            & a.sub-link{
              display: block;
              padding: 5px;
              margin-bottom: 5px;
              font-size: 13px;
              color: var(--color-blue);
              transition: transform 0.2s;
              &:hover{
                transform: translateX(3px);
              }
            }
          }
        }
        &:hover{
          background: #fff;
          & a.nav-link{
            color: var(--color-blue);
          }
          & ul.sub-list{
            transform: scale(1);
            opacity: 1;
            visibility: visible;
          }
        }
      }
    }
  }
}
