// ======POPUP==================================================================================
.popup{
   position: fixed;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   background: rgba(0, 0, 0, 0.767);
   display: flex;
   justify-content: center;
   align-items: center;
   opacity: 0;
   visibility: hidden;
   transition: opacity 0.5s;
   z-index: 999;
   &__container{
      position: relative;
     width: 500px;
     height: 400px;
     background: #fff;
     padding: 30px 20px;
     opacity: 0;
     transform: translateY(-20%);
     transition: transform 0.5s;
     & h3{
       display: inline-block;
       margin-bottom: 15px;
     }
     & p{
       font-size: inherit;
       line-height: 1.3;
     }
   }
   &.popup-open{
     opacity: 1;
     visibility: visible;
     transition: opacity 0.5s;
     & .popup__container{
       opacity: 1;
       transform: translateY(0);

     }
   }
   &-close{
      position: absolute;
      top: 10px;
      right: 10px;
      width: 20px;
      height: 20px;
      transition: transform 0.2s;
      &:before,
      &:after{
         content: '';
         position: absolute;
         top: 50%;
         left: 0;
         width: 100%;
         height: 1px;
         background: #111;
      }
      &:before{
         transform: rotate(45deg);
      }
      &:after{
         transform: rotate(-45deg);
      }
      &:hover{
        transform: scale(1.1);
      }
   }
 }
