.accordion {
  width: 1000px;
  &__item{
    position: relative;
    margin: 10px 20px;
    & .label{
      position: relative;
      padding: 10px;
      background: #2694af;
      color: #fff;
      cursor: pointer;
      &::before{
        content: '+';
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translateY(-50%);
        font-size: 1.5em;
      }
    }
    & .content{
      position: relative;
      background: #fff;
      height: 0;
      overflow: hidden;
      opacity: 0;
      transition: height 0.5s;
      overflow-y: auto;
    }
    &.active{
      & .label::before{
        content: '-';
      }
      & .content{
        height: 150px;
        padding: 10px;
        opacity: 1;
      }
    }
  }
  
}


